import Image from 'next/image';
import Link from 'next/link';
import { collection, getDocs } from 'firebase/firestore';
import { db } from '../../../lib/firebase'; // Adjust this path to your firebase config file

interface BlogPost {
  id: string;
  img: string;
  title: string;
  date: string;
  author: string;
  category: string;
}

const Blog4 = async () => {
  let blogContent: BlogPost[] = [];
  let error: string | null = null;

  try {
    const blogsCollection = collection(db, 'blogs');
    const blogsSnapshot = await getDocs(blogsCollection);
    console.log("Blogs datas is ",blogsSnapshot);
    blogContent = blogsSnapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data()
    })) as BlogPost[];
  } catch (err) {
    console.error('Error fetching blogs:', err);
    error = 'Failed to load blogs. Please try again later.';
  }

  // Format date helper function
  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    const day = date.getDate();
    const month = date.toLocaleString('en-US', { month: 'short' });
    return { day, month };
  };

  if (error) {
    return (
      <section className="news-section section-padding fix">
        <div className="container">
          <div className="text-center text-danger">
            <p>{error}</p>
          </div>
        </div>
      </section>
    );
  }

  return (
    <section className="news-section section-padding fix">
      <div className="container">
        <div className="row g-4">
          {blogContent.map((item, i) => {
            const { day, month } = formatDate(item.date);
            
            return (
              <div key={item.id} className="col-xl-4 col-md-6 col-lg-6 wow fadeInUp" data-wow-delay=".3s">
                <div className="news-card-items-3 style-4">
                  <div className="news-image">
                    <Image 
                      src={item.img} 
                      alt={item.title} 
                      width={416} 
                      height={347}
                    />
                  </div>
                  <div className="news-content">
                    <ul className="post-meta">
                      <li className="post">
                        {day}<span>{month}</span>
                      </li>
                      <li>
                        <i className="bi bi-person"></i>
                        By {item.author || 'Admin'}
                      </li>
                      <li>
                        <i className="bi bi-tag-fill"></i>
                        {item.category || 'Travel'}
                      </li>
                    </ul>
                    <h4>
                      <Link href={`/blog/blog-details/${item.id}`}>
                        {item.title}
                      </Link>
                    </h4>
                    <Link href={`/blog/blog-details/${item.id}`} className="link-btn">
                      Read More <i className="bi bi-arrow-right"></i>
                    </Link>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

export default Blog4;